<template>
  <div>
    <Loading :active="isLoading"></Loading>
    <div class="container">
      <div class="row mt-5 mb-5">
        <!-- 購物車列表 -->
        <div class="col-md-6 ps-4 pe-4">
          <h3 class="text-center"> 購物車清單</h3>
            <table class="table align-middle mt-2">
              <thead>
                <tr>
                  <th></th>
                  <th>圖片</th>
                  <th>品名</th>
                  <th style="width: 120px">數量</th>
                  <th>單價</th>
                </tr>
              </thead>
              <tbody>
                <template v-if="cart.carts">
                  <tr v-for="item in cart.carts" :key="item.id">
                    <td>
                      <a
                        :disabled="status.loadingItem === item.id"
                        @click="removeCartItem(item.id)"
                        role="button"
                      >
                        <i class="bi bi-x-circle text-danger"></i>
                      </a>
                    </td>
                    <td style="width: 100px">
                      <div class="cartImgSize"
                        
                        :style="{
                          backgroundImage: `url(${item.product.imageUrl})`
                        }"
                      ></div>
                    </td>
                    <td>
                      {{ item.product.title }}
                      <div class="text-success" v-if="item.coupon">
                        已套用優惠券
                      </div>
                    </td>
                    <td >
                      <div class="input-group input-group-sm ">
                        <input
                          type="number"
                          class="form-control"
                          min="1"
                          :disabled="item.id === status.loadingItem"
                          @change="updateCart(item)"
                          v-model.number="item.qty"
                        />
                        <div class="input-group-text">
                          / {{ item.product.unit }}
                        </div>
                      </div>
                    </td>
                    <td class="text-end">
                      <small
                        v-if="cart.final_total !== cart.total"
                        class="text-success"
                        >折扣價：</small
                      >
                      {{ $filters.currency(item.final_total) }}
                    </td>
                  </tr>
                </template>
              </tbody>
              <tfoot>
                <tr>
                  <td colspan="4" class="text-end">總計</td>
                  <td class="text-end">{{ $filters.currency(cart.total) }}</td>
                </tr>
                <tr v-if="cart.final_total !== cart.total">
                  <td colspan="3" class="text-end text-success">折扣價</td>
                  <td class="text-end text-success">
                    {{ $filters.currency(cart.final_total) }}
                  </td>
                </tr>
              </tfoot>
            </table>
            <div class="input-group mb-3 input-group-sm">
              <input
                type="text"
                class="form-control"
                v-model="coupon_code"
                placeholder="請輸入優惠碼"
              />
              <div class="input-group-append">
                <button
                  class="btn btn-outline-secondary"
                  type="button"
                  @click="addCouponCode"
                >
                  套用優惠碼
                </button>
              </div>
            </div>
        
        </div>
        <!-- 聯絡資料表單 -->
        <div class=" col-md-6 ps-4 pe-4">
          <h3 class="text-center">訂單資料</h3>
      <Form class="col-md-6 mt-2" v-slot="{ errors }"
            @submit="createOrder"
            style="width: 100%">
        <div class="mb-3">
          <label for="email" class="form-label">Email</label>
          <Field id="email" name="email" type="email" class="form-control"
                   :class="{ 'is-invalid': errors['email'] }"
                   placeholder="請輸入 Email" rules="email|required"
                   v-model="form.user.email"></Field>
          <ErrorMessage name="email" class="invalid-feedback"></ErrorMessage>
        </div>

        <div class="mb-3">
          <label for="name" class="form-label">收件人姓名</label>
          <Field id="name" name="姓名" type="text" class="form-control"
                   :class="{ 'is-invalid': errors['姓名'] }"
                   placeholder="請輸入姓名" rules="required"
                   v-model="form.user.name"></Field>
          <ErrorMessage name="姓名" class="invalid-feedback"></ErrorMessage>
        </div>

        <div class="mb-3">
          <label for="tel" class="form-label">收件人電話</label>
          <Field id="tel" name="電話" type="tel" class="form-control"
                   :class="{ 'is-invalid': errors['電話'] }"
                   placeholder="請輸入電話" rules="required"
                   v-model="form.user.tel"></Field>
          <ErrorMessage name="電話" class="invalid-feedback"></ErrorMessage>
        </div>

        <div class="mb-3">
          <label for="address" class="form-label">收件人地址</label>
          <Field id="address" name="地址" type="text" class="form-control"
                   :class="{ 'is-invalid': errors['地址'] }"
                   placeholder="請輸入地址" rules="required"
                   v-model="form.user.address"></Field>
          <ErrorMessage name="地址" class="invalid-feedback"></ErrorMessage>
        </div>

        <div class="mb-3">
          <label for="message" class="form-label">留言</label>
          <textarea name="" id="message" class="form-control" cols="30" rows="10"
                    v-model="form.message"></textarea>
        </div>
        <div class="text-center">
          <button class="btn btn-danger">送出訂單</button>
        </div>
      </Form>
    </div>
      </div>
       
    </div>
       
  </div>
</template>

<script>
export default {
  data() {
    return {
      products: [],
      product: {},
      status: {
        loadingItem: ""
      },
      form:{
          user:{
              name: '',
              email: '',
              tel: '',
              address: '',
          }
      },
      message: '',
      cart: {},
      coupon_code: ""
    };
  },
  methods: {
    getProducts() {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/products/all`;
      this.isLoading = true;
      this.$http.get(url).then(res => {
        this.products = res.data.products;
        console.log("products:", res);
        this.isLoading = false;
      });
    },
    getProduct(id) {
      this.$router.push(`/user/product/${id}`);
    },
    addCart(id) {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`;
      this.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty: 1
      };
      this.$http.post(url, { data: cart }).then(res => {
        // console.log(res);
        this.status.loadingItem = "";
        this.getCart();
      });
    },
    getCart() {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart`;
      this.isLoading = true;
      this.$http.get(url).then(res => {
        this.cart = res.data.data;
        this.isLoading = false;
        // console.log(res);
      });
    },
    updateCart(item){
        const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${item.id}`;
        this.isLoading = true;
        this.status.loadingItem = item.id
        const cart = {
            product_id: item.product_id,
            qty: item.qty 
        }
        this.$http.put(url,{data: cart}).then((res)=>{
            // console.log(res);
            this.getCart();
            this.status.loadingItem = "";
        })
    },
    addCouponCode(){
        const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/coupon`;
        const coupon = {
            code:this.coupon_code
        };
        this.$http.post(url,{data: coupon}).then((res)=>{
            // console.log(res);
            this.getCart()
            this.isLoading = false;
        })
    },
    removeCartItem(id){
        const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/cart/${id}`;
        this.isLoading = true;
        this.$http.delete(url).then((res)=>{
            this.status.loadingItem = ''
            // console.log(res);
            this.getCart();
            this.isLoading = false;

        })
    },
    createOrder(){
        const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/order`;
        const order = this.form;
        this.$http.post(url,{data: order}).then((res)=>{
            console.log(res);
            // this.$router.push(`/user/checkout/${this.orderId}`);
        })
    }
  },
  created() {
    this.getProducts();
    this.getCart();
  }
};
</script>

<style>
.cartImgSize{
  height: 50px;
  width: 50px;
  background-size: contain; 
  background-position: center;  
  background-repeat:no-repeat;
}
</style>